*{
	padding: 0;
	margin: 0;
}
body{
	min-width: 320px;
	font-family: @openSans;
	font-size: 16px;
	line-height: @line-height;
	color: @black;
	background: @bodyBg;
}
img{
	max-width: 100%;
	height: auto;
}
a{
    font-family: @openSans;  
    font-size: 14px; 
    color: @black;
    text-transform: uppercase;
    text-decoration: none;
    &:focus,
    &:active,
    &:hover{
    	outline: none;
        text-decoration: none;
        color: inherit;
    }
}
h3{
	font-size: 20px;
	font-weight: bold;
}
ul{
	list-style: none;
}
