.main-slogan{
	padding: 20px 0;
	background: @white;
	border-top: 1px solid @borderGray;
	border-bottom: 3px solid @borderGray;
	h1{
		font-size: 26px;
		line-height: 30px;
		font-weight: normal;
		text-align: center;
		color: @black;
	}
}
@media only screen and (max-width: 992px) {
   .main-slogan{
   	padding: 15px 0;
	   	h1{
	   		font-size: 18px;
	   		line-height: 20px;
	   	}
   }
}


/* Small Devices, Tablets */
@media only screen and (max-width: 768px) {
    .main-slogan{
   		padding: 8px 0;
	   	h1{
	   		font-size: 12px;
	   	}
   }
    
}


/* Extra Small Devices, Phones */

@media only screen and (max-width: 480px) {
    html{
        font-size: 8px;
    }
}