.offers{
	padding: 40px 0;
	background: @offersBg;
	border-bottom: 1px solid #9e9e9e;
	&__title{
		h3{
			margin-bottom: 10px;
			font-size: 24px;
			font-weight: normal;
		}
		p{
			font-size: 18px;
			font-weight: lighter;
			line-height: 20px;
		}
	}
	&__input{
		padding-left: 10px;
		padding-right: 10px;
		width: 100%;
		height: 50px;
		background: @white;
		border: none;
		outline: none;
		border: 1px solid @borderGray;
		border-radius: 3px;
	}
	&__btn{
		width: 100%;
		height: 50px;
		font-size: 18px;
		font-weight: bold;
		text-transform: capitalize;
		color: @white;
		background: @darkOrange;
		border: none;
		border-radius: 5px;
		border-bottom: 4px solid darken(@darkOrange, 10%);
		transition: all .5s;
		&:hover{
			background: darken(@darkOrange, 10%);
		}
	}
}
@media only screen and (max-width: 1200px) {
    .offers__title{
    	margin-bottom: 30px;
    }
}


/* Medium Devices, Desktops */

@media only screen and (max-width: 992px) {
   .offers{
   	&__input{
   		margin-bottom: 20px;
   	}
   }
}


/* Small Devices, Tablets */
@media only screen and (max-width: 768px) {
    html{
        font-size: 10px;
   }
    
}


/* Extra Small Devices, Phones */

@media only screen and (max-width: 400px) {
    .offers{
    	&__title{
    		h3{
    			font-size: 20px;
    		}
    		p{
    			font-size: 14px;
    		}
    	}
    }
}