.footer-content{
	padding-top: 35px;
	padding-bottom: 55px;
	color: @lightGray;
	background: @footerBg;
}
.footer-menu{
	h3{
		margin-bottom: 30px;
		font-size: 14px;
		font-weight: bold;
		text-transform: uppercase;
	}
	ul a{
		font-size: 14px;
		line-height: 24px;
		text-transform: capitalize;
		color: inherit;
		&:hover{
			text-decoration: underline;
		}
	}
}
.footer-social{
	text-align: right;
}
.footer-bottom{
	padding: 30px 0;
	padding-bottom: 50px;
}
.footer-copyright{
	font-size: 14px;
	color: @black;
}
.footer-icons{
	text-align: right;
}

@media only screen and (max-width: 1200px) {
    html{
        font-size: 14px;
    }
}


/* Medium Devices, Desktops */

@media only screen and (max-width: 992px) {
   .footer-menu__item{
   	margin-bottom: 20px;
   }
   .footer-social{
   	text-align: left;
   }
   .footer-icons{
   	float: left;
   }
   .footer-copyright{
   	margin-bottom: 20px;
   }
}


/* Small Devices, Tablets */
@media only screen and (max-width: 768px) {
    html{
        font-size: 10px;
   }
    
}


/* Extra Small Devices, Phones */

@media only screen and (max-width: 480px) {
    html{
        font-size: 8px;
    }
}