.main-content{
	padding-top: 48px;
	.banner-row{
		margin-bottom: 30px;
	}
	&__title{
		margin-left: 15px;
	}
}
.products-row{
	padding: 60px 0;
}
.product{
	position: relative;
	padding: 30px 15px;
	text-align: center;
	background: @white;
	border: 1px solid @borderGray;
	overflow: hidden;
	transition: all .5s;
	&__favorites{
		position: absolute;
		top: 10px;
		right: 15px;
		color: @heartColor;
	}
	&__sale{
		position: absolute;
		top: 14px;
		left: 17px;
		width: 50px;
		height: 50px;
		line-height: 50px;
		text-align: center;
		text-transform: uppercase;
		border: 1px solid @borderGray;
		border-radius: 50%;
	}
	&__img{
		margin-bottom: 10px;
		height: 200px;
		overflow: hidden;
	}
	&__description{
		height: 80px; 
		color: @black;
		overflow: hidden;
		h4{
			margin-bottom: 10px;
			font-size: 14px;
			text-transform: uppercase;
			text-align: center;
		}
		p{
			font-size: 14px;
			line-height: 20px;
		}
	}
	&__buy{
		padding: 5px 0;
		.price{
			margin-bottom: 30px;
			font-size: 18px;
			font-weight: bold;
			color: @red;
		}
	}
}
.product:hover{
	box-shadow: 0 0 25px rgba(0,0,0,.4);
}
.product:hover .btn-default{
	background: @bodyBg;
}

@media only screen and (max-width: 1200px) {
    html{
        font-size: 14px;
    }
}


/* Medium Devices, Desktops */

@media only screen and (max-width: 992px) {
   .main-content{
   	padding-bottom: 0;
   }
}


/* Small Devices, Tablets */
@media only screen and (max-width: 768px) {
    .product{
    	margin: 0 auto;
    	margin-bottom: 10px;
    	width: 80%;
    }
    .banner-row{
    	text-align: center;
    	img{
    		margin-bottom: 10px;
    	}
    }
    
}


/* Extra Small Devices, Phones */

@media only screen and (max-width: 480px) {
    html{
        font-size: 8px;
    }
}