.sidebar{
	padding-top: 48px;
	padding-bottom: 100px;
	border-right: 3px solid @borderGray;
	.widget{
		margin-bottom: 30px;
		padding-bottom: 20px;
		border-bottom: 3px solid @borderGray;
	}
	.show-more{
		position: relative;
	}
	.show-more a{
		color: @textBlue;	
	}
	.open-list{
		position: absolute;
		top: 25;
		left: 0;
		display: none;
		padding-left: 10px;
		width: 250px;
		background: @bodyBg;
		z-index: 1010;
		border: 2px solid darken(@bodyBg, 20%);
		border-top: none;
		box-shadow: 0 2px 7px rgba(0,0,0,0.2);
	}
	.open-list a{
		color: @black;
	}
	h3{
		margin-bottom: 30px;
	}
	a{
		display: block;
		color: @black;
		line-height: 28px;
		text-transform: capitalize;
		&:hover,
		&:focus,
		&:active{
			color: lighten(@black, 40%);
		}
	}
}
.sidebar-slider{
	margin-bottom: 35px;
	max-width: 226px;
	overflow: hidden;
	&__text{
		padding: 18px;
		font-size: 10px;
		line-height: 14px;
		text-align: center;
		background: @white;
		border-bottom: 1px solid @borderGray;
		h4{
			margin-bottom: 18px;
			font-size: 14px;
		}
	}
	.slick-dots{
		position: relative;
		bottom: 0;
		left: 0;
		display: block;
		margin-left: 0;
		padding: 0;
		padding-bottom: 10px;
		width: 100%;
		border: none;
		box-shadow: none;
		li{
			margin: 0 5px;
		}
	}
}
.sidebar-banner{
	display: block;
	margin-bottom: 40px;
	max-width: 225px;
	font-size: 18px;
	text-transform: uppercase;
	text-align: center;
	background: @white;
	box-sizing: border-box;
	.upper{
		font-size: 26px;
		font-weight: bold;
	}
	&__text{
		padding: 30px 20px;
		p{
			font-size: 18px;
			margin-bottom: 20px;
			line-height: 24px;
		}
	}
	.btn-red{
		padding: 8px 22px;
		text-transform: uppercase;
		border: none;
		border-radius: 0;
		outline: none;
	}
}
.about-store{
	max-width: 226px;
	padding-left: 15px;
	h3{
		margin-bottom: 28px;
	}
	p{
		margin-bottom: 30px;
		font-size: 14px;
		line-height: 20px;
	}
	.blue{
		color: @textBlue;
	}
}

@media only screen and (max-width: 1200px) {
    .sidebar-slider,
    .sidebar-slider .slick-dots,
    .sidebar-banner{
		margin-right: 16px;
    }
}


/* Medium Devices, Desktops */

@media only screen and (max-width: 992px) {
   .sidebar{
   	border-right: none;
   	padding-top: 0;
   	padding-bottom: 30px;
	.widget{
		padding-left: 50px;
	}
   }
   .about-store{
   	margin: 0 auto;
   	text-align: center;
   }
   .sidebar-slider,
    .sidebar-slider .slick-dots,
    .sidebar-banner{
		margin-right: 0;
    }
}


/* Small Devices, Tablets */
@media only screen and (max-width: 768px) {
    
    
}

@media only screen and (max-width: 540px) {
 	main .sidebar .col-xs-6{
 		width: 100%;
 	}
 	.sidebar .row:nth-of-type(2) .col-xs-6{
		text-align: center;
 	}
    .sidebar-slider,
    .sidebar-slider .slick-dots,
    .sidebar-banner{
		display: inline-block;
		max-width: 225px;
    }
}