.bottom-slider{
	padding: 50px 0;
	background: @white;
	.slick-next,
	.slick-next:focus, 
	.slick-next:hover,
	.slick-prev, 
	.slick-prev:focus, 
	.slick-prev:hover{
		background-image: url("../img/bottom-slider-arrows.png");
	}
	.slick-prev,
	.slick-next{
		width: 25px;
		height: 25px;
	}
	.slick-prev{
		left: -90px;
	}
	.slick-next{
		right: -90px;
		background-position: -28px 0;
	}
	ul{
		margin: 0 auto;
		width: 80%;
	}
}